/** 常用色值 **/
/** 常用字体大小 **/
.header {
  background-color: #fff;
}
.header .header-top {
  height: 60px;
  line-height: 60px;
  padding: 0 20px;
  text-align: right;
  border-bottom: 1px solid #e8e8e8;
}
.header .header-top .second-header-logo {
  text-align: left;
  font-size: 20px;
}
.header .header-top .second-header-logo .iconfont {
  font-size: 35px;
  vertical-align: middle;
}
.header .header-top a {
  margin-left: 40px;
  color: #6699cc;
}
.header .breadcrumb {
  height: 43px;
  line-height: 43px;
  padding: 0 20px;
}
.header .breadcrumb .breadcrumb-title {
  position: relative;
  text-align: center;
  font-size: 18px;
}
.header .breadcrumb .breadcrumb-title::after {
  position: absolute;
  content: '';
  left: calc(44%);
  top: 40px;
  border-top: 10px solid white;
  border-left: 12px solid transparent;
  border-right: 12px solid transparent;
}
.header .breadcrumb .weather {
  text-align: right;
}
.header .breadcrumb .weather .date {
  margin-right: 10px;
}
